<template lang="html">
  <div class="container-fluid">
    <div class="row">
      <div class="col-lg-12" v-if="message">
        <div class="alert alert-danger" role="alert">
          <strong>!</strong> 
        </div>
      </div>

      <div class="col-lg-6 col-lg-offset-3">
        <div class="panel panel-warning text-center">
          <div class="panel-heading">
            <h3 class="panel-title">
              
            </h3>
          </div>
          <div class="panel-body">
            <form>
              <div class="form-group">
                <label class="sr-only" for="name"></label>
                <input type="text" class="form-control" id="name" placeholder="" v-model="name" required>
              </div>
              <div class="form-group">
                <label class="sr-only" for="ic"></label>
                <input type="text" class="form-control" id="ic" placeholder="" v-model="ic" required>
              </div>
              <div class="form-group">
                <label class="sr-only" for="phone"></label>
                <input type="phone" class="form-control" id="phone" placeholder="" v-model="phone" required>
              </div>
              <div class="form-group">
                <label class="sr-only" for="email"></label>
                <input type="mail" class="form-control" id="email" placeholder="" v-model="email" required>
              </div>
              <div class="form-group">
                <p>
                  
                </p>
                <div class="radio">
                  <label>
                    <input type="radio" name="food1" id="food1" value="0" v-model="food">
                    
                  </label>
                  <label>
                    <input type="radio" name="food2" id="food2" value="1" v-model="food">
                    
                  </label>
                </div>
              </div>
              <div class="form-group">
                <button type="submit" class="btn btn-success" @click.prevent="userSignup"></button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      name: '',
      ic: '',
      phone: '',
      email: '',
      food: 0,
      message: false
    }
  },
  mounted () {
    this.name = this.$store.state.userInfo.name
  },
  computed: {
    id () {
      return this.$store.state.userInfo.id
    },
    checkName () {
      return this.name.length !== 0
    },
    checkIC () {
      return this.ic.length !== 0
    },
    checkEMail () {
      return this.email.length !== 0
    },
    checkPhone () {
      return this.phone.length !== 0
    }
  },
  methods: {
    userSignup () {
      if (this.checkName && this.checkIC && this.checkEMail && this.checkPhone) {
        this.$http.post('/apis/activities/signup', {
          token: window.localStorage.getItem('userLoginToken'),
          id: this.id,
          name: this.name,
          ic: this.ic,
          phone: this.phone,
          email: this.email,
          food: this.food
        }).then((res) => {
          if (res.data.success) {
            window.alert('')
            this.$router.push({name: 'me'})
          } else {
            window.alert('')
            this.$router.push({name: 'me'})
          }
        })
      } else {
        this.message = true
      }
    }
  }
}
</script>

<style lang="css">
</style>



