<script>
export default {
  data () {
    return {
      notic: []
    }
  },
  mounted () {
    this.loading = true
    this.getInfo()
  },
  methods: {
    getInfo () {
      this.$http.get('/apis/info')
        .then((result) => {
          let {notic} = result.data.data

          this.notic = notic
        })
    }
  }
}
</script>

